<template>
  <div class="login">
    <div class="top">
      <div class="toppic"></div>
    </div>
    <div class="loginform">
      <van-tabs>
        <van-tab title="账号登录" name="a">
          <van-form @submit="dologin">
            <van-field
            v-model="account"
              left-icon="graphic"
              name="account"
              placeholder="请输入手机号码"
              :rules="[{ required: true, message: '请输入手机号码' }]"
            />
            <van-field
            v-model="password"
              left-icon="lock"
              type="password"
              name="password"
              placeholder="填写登录密码"
              :rules="[{ required: true, message: '填写登录密码' }]"
            />
            <div style="margin: 16px">
              <van-button
                color="linear-gradient(90deg,#f35447 0,#ff8e3c)"
                round
                block
                type="info"
                native-type="submit"
                >登录</van-button
              >
            </div>
            <div class="tip">没有账号?<span>立即注册</span></div>
          </van-form>
        </van-tab>
        <van-tab title="快速登录" name="b">
          <van-form @submit="doSmsLogin">
            <van-field
              left-icon="graphic"
              name="用户名"
              placeholder="请输入手机号码"
               v-model="mobile"
            />
            <van-field
              left-icon="lock"
              type="password"
              name="密码"
              placeholder="填写验证码"
              v-model="sms"
            >
              <template #button>
                <van-button size="small" @click.stop.prevent="getSms" :disabled="countdown<60" type="warning">{{countdown>=60?"获取验证码":`${countdown}秒`}}</van-button>
              </template>
            </van-field>
            <div style="margin: 16px">
              <van-button
                color="linear-gradient(90deg,#f35447 0,#ff8e3c)"
                round
                block
                type="info"
                native-type="submit"
                >登录</van-button
              >
            </div>
            <div class="tip">没有账号?<span>立即注册</span></div>
          </van-form>
        </van-tab>
      </van-tabs>
    </div>
    <div class="bottom"></div>
  </div>
</template>
<script>
import {mapMutations,mapActions} from 'vuex'
export default {
  data() {
    return {
      account:'',
      password:'',
      mobile:'',
      sms:'',
      smskey:'',
      countdown:60
    }
  },
  created() {
     this.axios.get('/api/verify_code').then(d=>{
        console.log(d);
        this.smskey=d.data.data.key;//获取发送短信的key
      });
  },
  methods: {
    dologin(value){
      console.log(value);
      this.$axios.post('/api/login',value).then(d=>{
        console.log(d);
        if(d.data.status==200){
          this.saveToken(d.data.data.token);
          this.getLoginInfo(d.data.data.token);
          this.$toast({
            message:d.data.msg,
            onClose:()=>{
              console.log(666);
              this.$router.replace("/my")
            }
          })
        }else{
          this.$toast(d.data.msg)
        }
      })
    },
    doSmsLogin(){//验证码登录
        this.$axios.post('/api/login/mobile',{
          phone:this.mobile,
          captcha:this.sms
        }).then(d=>{
          //判断登录结果
          if(d.data.status===200){
            //登录成功
            let token=d.data.data.token;
            this.saveToken(token);
            this.getLoginInfo();
            this.$toast({
                message:"登录成功",
                icon:"passed",
                onClose:()=>{//但提示关闭时候回调函数
                    this.$router.push('/my');
                }
            });
          }else{
            this.$toast(d.data.msg);
          }
          console.log('验证码登录结果',d);
        })
      },
      getSms(){
        let mobile=this.mobile;
        //2.获取短信接口
        this.axios.post('api/register/verify',{//post数据
            key:this.smskey,//发送短信的key
            phone:mobile,//手机号
            type:'login'//短信类型为登录
        }).then(d=>{
          d=d.data;
          console.log('获取短信的结果==>',d);
          if(d.status==200){
            //如果发送成功, 发送按钮禁用. 同时有一个倒计时.60秒
            let intervalid=setInterval(()=>{
              if(this.countdown<=0){
                clearInterval(intervalid);
                this.countdown=60;
                return;
              }
              this.countdown--;
            },1000);
            this.$toast('验证码发送成功!');
          }else{
            this.$toast(d.msg);
          }
        })
      },
    ...mapMutations(['saveToken']),
    ...mapActions(['getLoginInfo'])
  },
}
</script>

<style lang="less">
.login {
  width: 100%;
  height: 100vh;
  overflow: hidden;
  background-image: linear-gradient(180deg, #eb5447 0, #ff8e3b);
  .top {
    width: 100%;
    height: 108px;
    padding-top: 35px;
    background: url("../assets/registerw.b3c17e1c.png") no-repeat center
      center/100% 100%;
    .toppic {
      width: 82px;
      height: 82px;
      margin: 0 auto;
      background-color: red;
      border-radius: 50%;
      border: 2px solid white;
    }
  }
  .loginform {
    width: 280px;
    // height: 282.9px;
    background-color: white;
    border-radius: 10px;
    margin: 0 auto;
    margin-top: 15px;
    padding: 22.5px 15px 0 15px;
    .van-tabs__wrap {
      .van-tabs__nav {
        width: 280px;
        height: 32.5px;

        .van-tab {
          font-size: 18px;
          font-weight: 700;
        }
      }
    }
    .van-tab__pane {
      .van-form {
        .van-cell {
          .van-field__button {
            width: 75px;
            height: 25px;
            .van-button {
              width: 75px;
              height: 25px;
              border: none;
              background-color: #f35446;
              padding: 0;
              border-radius: 15px;
              margin-left: 10px;
              .van-button__text {
                font-size: 12.5px;
                font-weight: normal;
                color: white;
              }
            }
          }
        }
      }
    }
    .van-button {
      .van-button__content {
        .van-button__text {
          font-size: 17px;
          font-weight: 700;
        }
      }
    }
    .tip {
      width: 280px;
      height: 55px;
      font-size: 15px;
      text-align: center;
      line-height: 55px;
      span {
        color: #fc4141;
      }
    }
  }
  .bottom {
    width: 310px;
    height: 18px;
    margin: 0 auto;
    background: url(data:image/png;base64,)
      no-repeat center center/100% 100%;
  }
}
</style>